<template>
  <div>
    <ReaderTutorial></ReaderTutorial>
    <header class="header" v-if="store.btnToggle">
      <h1 class="h1">Mulit-Author Interactive Novel Reader</h1>
      <button class="button" @click="importFile()">Import</button>
    </header>
    <div>
      <h1 class="h1">{{ novel.title }}</h1>
      <p v-if="novel.author">Written by {{ novel.author }}</p>
      <hr />
      <div v-for="(readContent, index) in readContents" :key="index" class="read-content" v-html="readContent"></div>
      <p class="content" v-html="highlightedCurrentSceneContent" @click="handleWordClick"></p>
    </div>
    <button style="all: unset; cursor: pointer;" @click="preScene">back to previous scene</button>&emsp;
    <input type="file" id="file-input" style="display: none" @change="onFileInputChange">
  </div>
</template>

<script>
import { useNovelStore } from '../stores/novelStore'
import { useStore } from '../stores/store'
import ReaderTutorial from '../components/tutorials/ReaderTutorial.vue';
export default {
  components: {
    ReaderTutorial,
  },
  setup() {
    const store = useStore();
    const novelStore = useNovelStore();
    return { store, novelStore }
  },
  //props: ['story'],
  data() {
    return {
      //灰色已阅读部分
      readContents: [],
      readingOrder: [],
      novel: {
        title: '',
        author: '',
        content: ''
      },
      paragraphs: [],
      currentScene: {
        sourceIndex: '',
        wordIndex: '',
        content: ''
      },
      index:0
      
    }
  },
  created() {
    this.fromCommunity();
  },
  computed: {
    highlightedCurrentSceneContent() {
      let content = this.currentScene.content;
      this.paragraphs.forEach((paragraph) => {
        if (paragraph.sourceIndex === this.currentScene.paragraphIndex) {
          const regex = new RegExp(`(${paragraph.wordIndex})`, 'gi');
          content = content.replace(regex, `<span class="highlighted-word">$1</span>`);
        }
      });
      return content;
    }
  },
  methods: {
    fromCommunity() {
      if (this.$route.query.startReading) {
        // Start reading automatically
        //console.log(this.novelStore.currentNovel);
        if (this.novelStore.currentNovel != null) {
          this.parseNovelText(this.novelStore.currentNovel);
          //console.log(this.novelStore.currentNovel);
        } else {
          alert("Please go to the community interface to select the book")
        }
      }
      return null;
    },
    preScene() {
      if (this.readingOrder.length > 1) {
        const preNode = this.readingOrder[this.readingOrder.length - 1];
        const preParagraph = this.paragraphs.find((paragraph) => {
          return paragraph.paragraphIndex === preNode;
        });
        this.readingOrder.pop(); // 移除当前场景
        this.readContents.pop(); // 移除最后一个已读段落
        if (preParagraph) {
          this.currentScene.sourceIndex = preParagraph.sourceIndex;
          this.currentScene.wordIndex = preParagraph.wordIndex;
          this.currentScene.content = preParagraph.content;
          this.currentScene.paragraphIndex = preParagraph.paragraphIndex;
        } else {
          alert("The previous scene was not found, please make sure you are not in the first scene");
        }
      } else {
        alert("Already the first scene, can't go back");
      }
    },
    importFile() {
      document.getElementById('file-input').click();
    },
    onFileInputChange(event) {
      const file = event.target.files[0];
      const reader = new FileReader();
      reader.onload = (event) => {
        const text = event.target.result;
        this.parseNovelText(text);
      };
      reader.readAsText(file);
    },
    parseNovelText(text) {
      const lines = text.trim().split('\n');
      let title = '';
      let author = '';
      let content = '';
      let l = 0;
      //this.store.btnToggle = !this.store.btnToggle;
      for (let line of lines) {
        if (line.startsWith('Story Title: ')) {
          title = line.slice(13);
        } else if (line.startsWith('Authors: ')) {
          author = line.slice(9);
        } else {
          content += line + '<br/>';
          l += 1
        }
      }
      this.novel.title = title;
      this.novel.author = author;
      this.novel.content = content;

      const nodes = content.split('<br/>');
      //必须每个都有一个，即使指向存在段落
      for (let i = 0; i < l + 1; i++) {
        this.paragraphs.push({
          sourceIndex: null,
          wordIndex: null,
          content: "",
          paragraphGoTo: i,
          paragraphIndex: i
        });
      }
      this.index = 0;
      nodes.forEach((paragraphText,i) => {
        const matches = paragraphText.match(/\[(\d+)\] \[(.*?)\] (.*) \[(\d+)\]/);
        if (matches) {
          const sourceIndex = parseInt(matches[1]);
          const wordIndex = matches[2];
          const content = matches[3].replaceAll('"', "'");
          const paragraphGoTo = parseInt(matches[4]);
          this.index += 1

          this.paragraphs[i].sourceIndex = sourceIndex;
          this.paragraphs[i].wordIndex = wordIndex === "" ? " " : wordIndex;
          this.paragraphs[i].content = content;
          this.paragraphs[i].paragraphGoTo = paragraphGoTo;   
          if (i === 0) {
            this.currentScene.sourceIndex = sourceIndex;
            this.currentScene.wordIndex = wordIndex;
            this.currentScene.content = content;
            //初始不可能是前往已存在下标
            this.currentScene.paragraphIndex = paragraphGoTo;
            this.readingOrder.push(this.currentScene.paragraphIndex);
            //this.readContents.push(this.currentScene.content);
          }
          //TODO: change the condition
          if (content.replaceAll(" ", "") === "GOTOEXISTINGNODE") {
            this.index -= 1
            //防止唯一下标重复
            this.paragraphs[i].paragraphIndex = 0;
          } else {
            this.paragraphs[i].paragraphIndex = paragraphGoTo;
          }
        }
      });
    },
    handleWordClick(event) {
      const selectedWord = event.target.closest('.highlighted-word');
      if (selectedWord) {
        const wordIndex = selectedWord.textContent.trim();
        let nextParagraph = this.paragraphs.find((paragraph) => {
          return paragraph.sourceIndex === this.currentScene.paragraphIndex
            && paragraph.wordIndex.toLowerCase() === wordIndex.toLowerCase()
        });
        //二次跳转
        if (nextParagraph && nextParagraph.paragraphIndex == 0) {
          nextParagraph = this.paragraphs.find((paragraph) => {
            return nextParagraph.paragraphGoTo === paragraph.paragraphIndex
          });
        }
        if (nextParagraph) {
          this.readContents.push(this.currentScene.content);
          this.readingOrder.push(this.currentScene.paragraphIndex);
          this.currentScene.sourceIndex = nextParagraph.sourceIndex;
          this.currentScene.wordIndex = nextParagraph.wordIndex;
          this.currentScene.content = nextParagraph.content;
          this.currentScene.paragraphIndex = nextParagraph.paragraphIndex;
        } else {
          alert("No scene is connected to this word.");
        }
      }
    },
  }
};
</script>
<style>
.header {
  display: flex;
  /* 将 header 标签的 display 属性设置为 flex */
  align-items: center;
  /* 垂直居中 */
  justify-content: space-between;
  /* 将子元素分散对齐 */
  padding: 10px;
  /* 添加 padding 以增加内容的内边距 */
}

.content {
  font-size: 2em;
  text-indent: 2em;
  text-align: left;
}


.h1 {
  display: inline-block;
  vertical-align: middle;
  /* 可以让按钮和标题在同一垂直位置 */
}


.read-content {
  font-size: 2em;
  text-indent: 2em;
  text-align: left;
  opacity: 0.5;
  pointer-events: none;
}

</style>
